<template>
  <v-container style="margin-top: 64px;">
    <h2>Contact Management</h2>

    <!-- Select a user -->
    <v-select v-model="selectedUser" :items="users" item-text="text" item-value="value" label="Select a user"
      style="width: 50%; min-width: 120px;"></v-select>

    <!-- Select a phone ID -->
    <v-select v-model="selectedPhoneId" :items="phoneIds" item-text="text" item-value="value" label="Select a Phone ID"
      style="width: 50%; min-width: 120px;"></v-select>

    <!-- Filter and Load Data -->
    <v-row>
      <v-col cols="12" md="6">
        <v-text-field v-model="projectFilter" label="Enter project name" outlined />
      </v-col>
      <v-col cols="12" md="6">
        <v-btn @click="loadContactsFromStrapi2" color="primary">Load Data</v-btn>
      </v-col>
    </v-row>

    <!-- Sticky Buttons Row -->
    <v-row class="sticky-buttons">
      <v-col cols="2" md="1">
        <v-btn @click="openAddForm" color="success" class="mb-4" icon>
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="2" md="1">
        <v-btn @click="editSelectedContact" :disabled="!selectedIndex" color="primary" class="mb-4" icon>
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="2" md="1">
        <v-btn @click="prevContact" :disabled="selectedIndex === 0" color="primary" class="mr-2" icon>
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="2" md="1">
        <v-btn @click="nextContact" :disabled="selectedIndex === contacts.length - 1" color="primary" icon>
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    

    
    <!-- Contacts Table -->
    <v-data-table v-if="!loading && !error && contacts.length && !currentContact && !isAdding" :headers="headers"
      :items="contacts" item-key="id" class="elevation-1" style="width: 100%">

      
      <template v-slot:item="{ item, index }">
        <tr :class="getRowClass(index)" @click="focusRow(item, index)">
          <td>{{ index + 1 }}</td>
          <td>{{ item.project }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.phoneNumber }}</td>
          <td>{{ item.interaction }}</td>
          <td>{{ item.status }}</td>
          <td>{{ item.stage }}</td>
          <td>{{ item.phoneId }}</td>
          <td>{{ item.user }}</td> 
          <td>{{ item.doc }}</td>
          <td>{{ item.rating }}</td>
          <td>{{ item.called }}</td>
          <td>{{ item.contacted }}</td>
          <!-- New fields -->
          <td>{{ item.clientType }}</td>
          <td>{{ item.address }}</td>
          <td>{{ item.URL }}</td>
          <td>{{ item.URLDate }}</td>
        </tr>
      </template>
    </v-data-table>

    <!-- Edit Contact Form Component -->
    <edit-contact-form v-if="currentContact && !isAdding" :contact="currentContact" :on-save="saveContact"
      :on-cancel="cancelEdit" :selectedUser="selectedUser" :selectedPhoneId="selectedPhoneId" :index="selectedIndex" :contacts="contacts" />

    <!-- Add New Record Form -->
    <v-form v-if="isAdding" ref="addForm">
      <v-row>
        <v-col cols="12" md="12">
          <h2>Add New Contact</h2>
          <v-text-field v-model="newContactData.project" label="Project" outlined></v-text-field>
          <v-text-field v-model="newContactData.name" label="Name" outlined></v-text-field>
          <v-text-field v-model="newContactData.phoneNumber" label="Phone" outlined></v-text-field>
          <v-text-field v-model="newContactData.status" label="Status" outlined></v-text-field>
          <v-text-field v-model="newContactData.rating" label="Rating" outlined></v-text-field>
          <v-checkbox v-model="newContactData.called" label="Called" class="edit-mode"></v-checkbox>
          <v-checkbox v-model="newContactData.contacted" label="Contacted" class="edit-mode"></v-checkbox>

          <!-- New fields -->
          <v-text-field v-model="newContactData.clientType" label="Client Type" outlined></v-text-field>
          <v-text-field v-model="newContactData.address" label="Address" outlined></v-text-field>
          <v-text-field v-model="newContactData.url" label="URL" outlined></v-text-field>
          <v-text-field v-model="newContactData.URLDate" label="URL Date" type="date" outlined></v-text-field>

          <v-btn @click="saveNewContact" color="success">Save</v-btn>
          <v-btn @click="cancelAdd" color="blue">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
import EditContactForm from './EditContactForm.vue';
import { ref } from 'vue';
import { loadContactsF, updateContact, addContact } from '../utils/contactUtils';

export default {
  components: {
    EditContactForm,
  },
  setup() {
    const contacts = ref([]);
    const loading = ref(false);
    const error = ref(null);
    const currentContact = ref(null);
    const isAdding = ref(false);
    const newContactData = ref({});
    const projectFilter = ref('');
    const selectedUser = ref(null);
    const selectedPhoneId = ref(null);
    const selectedIndex = ref(null);
    const selectedContact = ref(null);

    const headers = [
      { text: 'No', title: 'No', value: 'no' },
      { text: 'Project', title: 'Project', value: 'project' },
      { text: 'Name', title: 'Name', value: 'name' },
      { text: 'Phone Number', title: 'Phone Number', value: 'phoneNumber' },
      { text: 'Interaction', title: 'Interaction', value: 'interaction' },
      { text: 'Status', title: 'Status', value: 'status' },
      { text: 'Stage', title: 'Stage', value: 'stage' },
      { text: 'Phone ID', title: 'Phone ID', value: 'phoneId' },
      { text: 'User', title: 'User', value: 'user' },
      { text: 'Doc', title: 'Doc', value: 'doc' },
      { text: 'Rating', title: 'Rating', value: 'rating' },
      { text: 'Called', title: 'Called', value: 'called' },
      { text: 'Contacted', title: 'Contacted', value: 'contacted' },
      { text: 'Client Type', title: 'Client Type', value: 'clientType' },
      { text: 'Address', title: 'Address', value: 'address' },
      { text: 'URL', title: 'URL', value: 'URL' },
      { text: 'URL Date', title: 'URL Date', value: 'URLDate' },
    ];

    async function loadContactsFromStrapi2() {
      loading.value = true;
      error.value = null;
      try {
        contacts.value = await loadContactsF(
          projectFilter.value,
          contacts.value,
          new Date().toLocaleDateString('en-GB', { day: '2-digit', month: '2-digit', year: 'numeric' })
        );
      } catch (err) {
        console.error('Error loading contacts:', err);
        error.value = 'An error 211 occurred while loading contacts. Please try again.';
      } finally {
        loading.value = false;
      }
    }

    function focusRow(contact, index) {
      selectedContact.value = contact;
      selectedIndex.value = index;
    }

    function editSelectedContact() {
      if (selectedContact.value) {
        currentContact.value = { ...selectedContact.value };
      }
    }

    function prevContact() {
      if (selectedIndex.value > 0) {
        selectedIndex.value--;
        currentContact.value = contacts.value[selectedIndex.value];
      }
    }

    function nextContact() {
      if (selectedIndex.value < contacts.value.length - 1) {
        selectedIndex.value++;
        currentContact.value = contacts.value[selectedIndex.value];
      }
    }

    function openAddForm() {
      newContactData.value = {};
      isAdding.value = true;
    }

    async function saveNewContact() {
      try {
        await addContact(newContactData.value);
        contacts.value.push({ ...newContactData.value });
        isAdding.value = false;
      } catch (err) {
        console.error('Error saving contact:', err);
      }
    }

    function cancelAdd() {
      isAdding.value = false;
    }

    function cancelEdit() {
      currentContact.value = null;
    }

    
    function getRowClass(index) {
      return index === selectedIndex.value ? 'highlighted-row' : (index % 2 === 0 ? 'even-row' : 'odd-row');
    }

    async function saveContact(updatedData) {
      if (currentContact.value.id) {
        await updateContact(currentContact.value.id, updatedData);
      }
      loadContactsFromStrapi2();
      currentContact.value = null;
    }

    // async function saveContact(contact, index) {
    //   try {
    //     await updateContact(contact);
    //     contacts.value[index] = contact;
    //     currentContact.value = null;
    //   } catch (err) {
    //     console.error('Error updating contact:', err);
    //   }
    // }

    return {
      getRowClass,
      users: ["Alyssa", "Ashton", "Alvin"],
      phoneIds: ["Alyssa H", "Ashton H", "Alvin H"],
      headers,
      contacts,
      loading,
      error,
      currentContact,
      isAdding,
      newContactData,
      projectFilter,
      selectedUser,
      selectedPhoneId,
      selectedIndex,
      focusRow,
      prevContact,
      nextContact,
      openAddForm,
      saveNewContact,
      cancelAdd,
      editSelectedContact,
      saveContact,
      cancelEdit,
      loadContactsFromStrapi2,
    };
  },
};
</script>

<style scoped>
.sticky-buttons {
  position: sticky;
  top: 0;
  background: white;
  z-index: 1000;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.highlighted-row {
  background-color: #ffe575;
}

.even-row {
  background-color: #fecede;
}

.odd-row {
  background-color: white;
}
</style>
