export function convertYMD2DMY(dateString) {
  // Check if dateString is valid
  if (!dateString) return null;

  // Parse the date string to a Date object
  const date = new Date(dateString);

  // Check if the date is the default invalid date (1900-01-01)
  if (date.getFullYear() === 1900 && date.getMonth() === 0 && date.getDate() === 1) {
    return null;
  }

  // Format the date as DD/MM/YYYY
  const dayStr = String(date.getDate()).padStart(2, '0');
  const monthStr = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const yearStr = date.getFullYear();

  return `${dayStr}/${monthStr}/${yearStr}`;
}
// Update a contact
export async function updateContact(id, updatedData) {
  // Check if URLDate exists and is valid before converting it
  if (updatedData.URLDate !== undefined && updatedData.URLDate !== null && updatedData.URLDate !== "") {
    updatedData.URLDate = convertDMY2YMD(updatedData.URLDate);
  } else {
    // Optionally, you can choose to remove URLDate if it's undefined/null
    delete updatedData.URLDate; // This will omit the field from the mutation if it's undefined
  }

  const mutation = gql`
    mutation updateLead($id: ID!, $data: LeadInput!) {
      updateLead(id: $id, data: $data) {
        data {
          id
          attributes {
            name
            phoneNumber
            project
            called
            contacted
            phoneId
            clientType  
            address     
            URL         
            URLDate     
          }
        }
      }
    }
  `;

  try {
    const { data } = await client.mutate({
      mutation,
      variables: {
        id,
        data: updatedData,
      },
    });

    return data.updateLead.data;
  } catch (error) {
    console.error('Error updating contact:', error);
    throw error;
  }
}



// Add a new contact
export async function addContact(newContact) {
  const mutation = gql`
    mutation createContact($data: ContactInput!) {
      createContact(data: $data) {
        data {
          id
          attributes {
            name
            phoneNumber
            project
            called
            contacted
          }
        }
      }
    }
  `;

  try {
    const { data } = await client.mutate({
      mutation,
      variables: {
        data: newContact,
      },
    });

    return data.createContact.data;
  } catch (error) {
    console.error('Error adding contact:', error);
    throw error;
  }
}
