<template>
    <v-form v-if="contact" ref="contactForm">
      <v-row>
        <v-col cols="12" md="12">
          <h2>Edit Contact: {{ index }}-{{ contact.no }}/{{ contact.name }}/{{ contact.phoneNumber }}</h2>
  
          <!-- Existing fields -->
          <v-textarea v-model="editContactData.interaction" label="Interaction" outlined></v-textarea>
          <v-text-field label="Doc" v-model="editContactData.doc" class="edit-mode"></v-text-field>
          <v-text-field label="Status" v-model="editContactData.status" class="edit-mode"></v-text-field>
          <v-text-field label="Rating" v-model.number="editContactData.rating" type="number" class="edit-mode"></v-text-field>
          <v-text-field label="Called (0 or 1)" v-model.number="editContactData.called" type="number" class="edit-mode"></v-text-field>
          <v-text-field label="Contacted (0 or 1)" v-model.number="editContactData.contacted" type="number" class="edit-mode"></v-text-field>
  
          <!-- Dropdown for Client Type -->
          <v-select v-model="editContactData.clientType" :items="clientTypes" label="Client Type" outlined></v-select>
  
          <!-- Address and URL fields -->
          <v-text-field label="Address" v-model="editContactData.address" class="edit-mode"></v-text-field>
          <v-text-field label="URL" v-model="editContactData.URL" class="edit-mode"></v-text-field>
          <v-text-field label="URL Date" v-model="editContactData.URLDate" type="date" class="edit-mode"></v-text-field>
  
          <!-- Dropdown for Stage -->
          <v-select v-model="editContactData.stage" :items="stageOptions" label="Stage" outlined></v-select>
  
          <!-- Dropdown for PhoneId -->
          <v-select v-model="editContactData.phoneId" :items="phoneIdOptions" label="PhoneId" outlined></v-select>
  
          <h2>Last Call Date: {{ contact.lastCall }}</h2>
          <h2>Next Call Date: {{ contact.nextCall }}</h2>
  
          <v-btn @click="saveContact" color="primary">Save</v-btn>
          <v-btn @click="cancelEdit" color="blue">Cancel</v-btn>
  
          <v-btn rounded @click="makeCall2(editContactData, selectedUser, index, contacts)"
            :color="isPhoneDisabled(contact) ? 'grey' : 'primary'"
            style="width: 20%; min-width: 50px; height: 70px;"
            :disabled="isPhoneDisabled2(contact) == 1 || isPhoneDisabled2(contact) == 0" class="mx-1">
            <v-icon v-if="isPhoneDisabled2(contact) == 1 || isPhoneDisabled2(contact) == 0">mdi-phone-off</v-icon>
            <v-icon v-else>mdi-monitor-cellphone-star</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
  </template>
  
  <script>
  import { useMutation } from '@vue/apollo-composable';
  import { ref, watch } from 'vue';
  import { UPDATE_CALLME, convertDMY2YMDtime, getDeviceInfo, isPhoneDisabled2, isPhoneDisabled } from '../utils/contactUtils';
  
  export default {
    name: "editFromy",
    props: {
      contact: Object,
      onSave: Function,
      onCancel: Function,
      index: Number,
      selectedUser: String,
      selectedPhoneId: String,
      contacts: Object,
    },
    setup(props) {
      const editContactData = ref({
        interaction: '',
        doc: '',
        status: '',
        rating: 0,
        called: 0,
        contacted: 0,
        clientType: '',  // Add clientType to editContactData
        address: '',     // New field for Address
        URL: '',         // New field for URL
        URLDate: '',     // New field for URL Date
        stage: '',       // New field for Stage
        phoneId: ''      // New field for PhoneId
      });
  
      // Define options for the dropdowns
      const clientTypes = ["buyer", "seller", "co broke", "Tenant", "Land Owner"];
      const stageOptions = ["Lead", "Opportunity", "Negotiation", "Closed", "Follow-up"]; // Add more stage options as needed
      const phoneIdOptions = ['Alyssa H','Ashton H','Alvin S' , '' ];
     
  
      // Watch for changes in the contact prop to update the form
      watch(
        () => props.contact,
        (newContact) => {
          if (newContact) {
            editContactData.value = {
              name: newContact.name || '',
              phoneNumber: newContact.phoneNumber || '',
              lastCall: convertDMY2YMDtime(newContact.lastCall, true),
              nextCall: convertDMY2YMDtime(newContact.nextCall, true),
              no: newContact.no,
              interaction: newContact.interaction || '',
              doc: newContact.doc || '',
              status: newContact.status || '',
              rating: newContact.rating || 0,
              called: newContact.called || 0,
              contacted: newContact.contacted || 0,
              clientType: newContact.clientType || '',
              address: newContact.address || '',
              URL: newContact.URL || '',
              URLDate: newContact.URLDate || '',
              stage: newContact.stage || '',
              phoneId: newContact.phoneId || ''
            };
          }
        },
        { immediate: true }
      );
  
      const updateCallCount = () => {
        if (!isPhoneDisabled(editContactData.value)) {
          editContactData.value.called = (function (calledString) {
            const numberValue = parseInt(calledString, 10);
            if (!isNaN(numberValue)) {
              return numberValue + 1;
            } else {
              return 1;
            }
          })(editContactData.value.called);
  
          editContactData.value.dialedDate = new Date().toLocaleDateString();
        }
      };
  
      const handleCallClick = () => {
        updateCallCount();
  
        const newLogEntry = {
          name: editContactData.value.name,
          phoneNumber: editContactData.value.phoneNumber,
          time: new Date().toLocaleString()
        };
  
        const callLog = [];
        callLog.push(newLogEntry);
  
        if (callLog.length > 8) {
          callLog.shift();
        }
      };
  
      const makeCall2 = async (contact, selectedUser, index, contacts) => {
        await makeCall(contact, selectedUser, index, contacts);
        handleCallClick();
      };
  
      const saveContact = () => {
        props.onSave(editContactData.value);
      };
  
      const cancelEdit = () => {
        props.onCancel();
      };
  
      const { mutate: updateCallme } = useMutation(UPDATE_CALLME);
  
      async function makeCall(contact, selectedUser, currentIndex, contacts) {
        try {
          let { name, doc, phoneNumber, status, interaction, lastCall, nextCall, no, clientType, address, URL, URLDate, stage, phoneId } = contact;
          lastCall = convertDMY2YMDtime(lastCall, true);
          nextCall = convertDMY2YMDtime(nextCall, true);
  
          let user = selectedUser;
          let id = 0;
          if (user.toLowerCase() === "alyssa") id = 3;
          if (user.toLowerCase() === "alvin") id = 4;
          if (user.toLowerCase() === "ashton") id = 2;
  
          const variables = {
            id: id, doc: doc, user: user, name: name, phoneNumber: phoneNumber, status: status,
            interaction: interaction, lastCall: lastCall, nextCall: nextCall, no: no,
            clientType: clientType, address: address, URL: URL, URLDate: URLDate, stage: stage, phoneId: phoneId
          };
  
          await updateCallme(variables);
        } catch (error) {
          alert("error 100");
          console.error('Error updating callme:', error);
        }
  
        currentIndex = contacts.findIndex(c => c.no === contact.no);
      }
  
      return {
        updateCallCount,
        handleCallClick,
        makeCall2,
        editContactData,
        saveContact,
        cancelEdit,
        isPhoneDisabled2,
        isPhoneDisabled,
        clientTypes,
        stageOptions,
        phoneIdOptions
      };
    },
    mounted() {
      getDeviceInfo();
    },
  };
  </script>
  
  <style scoped>
  .edit-mode {
    margin-bottom: 10px;
  }
  </style>
  